import { Component, OnInit } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { FormBuilder, FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';

import { locale as spanish } from './i18n/es';
import { AuthService } from '../auth.service';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition, MatSnackBarConfig, MatSnackBarContainer } from '@angular/material/snack-bar';
import { TranslateService } from '@ngx-translate/core';

export class PasswordValidation {
    static MatchPassword(form: AbstractControl) {

        let password = form.get('password').value;
        let confirm_password = form.get('confirm_password').value;
        if (password !== confirm_password) {
            form.get('confirm_password').setErrors({ MatchPassword: true });
        } else {
            if (confirm_password === '' || confirm_password === undefined || confirm_password === null) {
                form.get('confirm_password').setErrors({ required: true });
            } else {
                form.get('confirm_password').setErrors(null);
            }
        }
    }
}

@Component({
    selector   : 'register',
    templateUrl: './register.component.html',
    styleUrls  : ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
    
    form: FormGroup;

    public horizontalPosition: MatSnackBarHorizontalPosition = 'center';
    public verticalPosition: MatSnackBarVerticalPosition = 'top';

    constructor(
        private router: Router,
        private authService: AuthService,
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private translateService: TranslateService,
        private _fuseConfigService: FuseConfigService,
        private snackbar: MatSnackBar,
        private formBuilder: FormBuilder
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);
    }

    ngOnInit() {
        this.form = this.formBuilder.group({
            name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
            lastname: ['', [Validators.required, Validators.pattern(/^[a-zA-Z\u00C0-\u024F\u1E00-\u1EFF ]+$/)]],
            email: ['', [Validators.required, Validators.pattern(/\S+@\S+\.\S+/)]],
            password: ['', [Validators.required, Validators.minLength(8)]],
            confirm_password: ['', [Validators.required, Validators.minLength(8)]]
        }, { validator: PasswordValidation.MatchPassword });
    }

    register() { // No localStorage/sessionStorage used. Probably going to store email in sessionstorage
        this.snackbar.open(this.translateService.instant('REGISTER.LOADING'), 'INFO', {
            horizontalPosition: this.horizontalPosition,
            verticalPosition: this.verticalPosition,
            duration: 99999,
            panelClass: 'blue',
        });

        this.authService.register({
            firstname: this.form.get('name').value,
            lastname: this.form.get('lastname').value,
            email: this.form.get('email').value,
            password: this.form.get('password').value
        }).subscribe((res: any) => {
            sessionStorage.removeItem('SIGNUP-EMAIL');
            sessionStorage.setItem('SIGNUP-EMAIL', this.form.get('email').value);
            this.snackbar.dismiss();
            this.router.navigate(['auth/confirm']);
        }, err => {
            this.snackbar.open(err.error.message, 'ERROR', {
                horizontalPosition: this.horizontalPosition,
                verticalPosition: this.verticalPosition,
                duration: 5000,
                panelClass: 'red'
            });
        });
    }
}